html{
    height:100%;
    background:#3c0d6a
 
}

body{
    background:#3c0d6a

}

.wrapper{
    margin-left:40%;
    transition: 0.8s;
    height:100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    perspective: 150px;
}

.chest{
    
    width: 200px;
    height: 200px;
    margin-top:150px;
    opacity: 1;
    cursor:pointer;
    
    
    
}



.highPart{
    
    position: relative;
    z-index: 1;
    
    top: 0px;
    height:50%;
    width:100%;
    transform-style: preserve-3d;
    perspective: 150px;
    transform: rotateX(-30deg);
    transition:0.3s;
    transform-origin: center center;

}



.highPart div {
    
    transform-style: preserve-3d;
    position: absolute;

    background: #0f0f3d;
    border: 1px solid #fff;
}




.up {
    z-index: 200 !important;
    border-radius: 0px 0px 20px 20px;
    height: 100px;
    width: 200px;
    transform: rotateX(90deg) translateZ(50px);
}
.upfront {
    border-radius: 20px 20px 0px 0px;
    height: 100px;
    width: 200px;
    transform: translateZ(50px);
}
.upback {
    border-radius: 00px 00px 0px 0px;
    height: 100px;
    width: 200px;
    background-color:#dee2ff !important;
    transform: rotateX(-180deg) translateZ(50px);
}
.upleft {
    border-radius: 0px 20px 0px 0px;
    height: 100px;
    width: 100px;
    background-color:#f0efeb !important;
    transform: rotateY(-90deg) translateZ(50px);
}
.upright {
    border-radius: 20px 0px 0px 0px;
    height: 100px;
    width: 100px;
    background-color: #f0efeb !important;
    transform: rotateY(90deg) translateZ(150px);
}

.lowPart{

    z-index: 0;
    border-radius: 3px;
    height:50%;
    width:100%;
    transform-style: preserve-3d;
    perspective: 150px;
    transform: rotateX(-45deg)  translate(0px,0px);
    transition:0.3s;
}

.lowPart div {
    
    position: absolute;
    background: #0f0f3d;
    border: 1px solid #fff;
}

.down{
    box-shadow:20px 40px 200px 100px rgba(0,0,0,0.5);
    border-radius: 20px 20px 20px 20px ;
    height:100px;
    width:200px;
    
    transform: rotateX(90deg) translateZ(50px) translateZ(-100px);
}
.front {
    color:white;
    font-stretch: extra-expanded;
    text-align: center;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    line-height: 100px;
    font-size: 25px;

    border-radius: 0px 0px 20px 20px ;
    width:200px;
    height:100px;
    transform: translateZ(50px);
}
.back {
    background-color:#cbc0d3 !important;
    width: 200px;
    height:100px;
    transform: rotateX(-180deg) translateZ(50px);
}
.left {
    background-color:#f0efeb !important;
    border-radius: 0px 0px 20px 20px ;
    height:100px;
    width:100px;
    transform: rotateY(-90deg) translateZ(50px);
}
.right {
    background-color:#f0efeb !important;
    border-radius: 0px 0px 20px 20px ;
    width: 100px;
    height:100px;
    transform: rotateY(90deg) translateZ(150px);
}

.showfollow{
    width:100%;
    height:30px;
    border-radius: 3px;
    background-color: white;
    border: 1px solid #dee2ff;
}



